<template>
  <div class="base-line-charts" ref="baseLineCharts"></div>
</template>

<script>

export default {
  name: "base-line-charts",
  components: {

  },
  props: {
    chartsData: {
      type: Array,
      default: () => {
        []
      }
    },
    xNameData: {
      type: Array,
      default: () => {
        []
      }
    },
  },
  data() {
    return {

    }
  },
  mounted() {
    this.init()
  },
  methods: {
    init() {
      let option = {
        title: {
          show: false,
          text: '折线图堆叠'
        },
        tooltip: {
          trigger: 'axis'
        },
        // legend: {
        //   show: false,
        //   data: ['邮件营销']
        // },
        grid: {
          left: '3%',
          right: '4%',
          bottom: '3%',
          containLabel: true
        },
        toolbox: {
          feature: {
            saveAsImage: {}
          }
        },
        xAxis: {
          type: 'category',
          boundaryGap: false,
          data: this.xNameData,
          axisTick: {
            show: true  //坐标轴刻度线
          },
          axisLine: { //轴线
            show: false
          },
          splitLine: { //网格线
            show: true
          },
          axisLabel: { //坐标轴样式
            // interval: 0,
            // rotate:45,//倾斜度 -90 至 90 默认为0
            // margin:2,
            textStyle: {
              color: '#636363',
              fontWeight: "bolder",
            }
            // formatter: function (value) {
            //   //x轴的文字改为竖版显示
            //   var str = value.split("");
            //   return str.join("\n");
            // }
          },
        },
        yAxis: {
          type: 'value',
          name: '',
          axisLine: {
            show: false
          },
          axisTick: {
            show: false
          },
          axisLabel: {
            textStyle: {
              color: '#636363'
            }
          },
        },
        series: [
          {
            color: '#3c86e0',
            name: '',
            type: 'line',
            stack: '总量',
            data: this.chartsData
          }
        ]
      };
      let echarts = require('echarts');
      this.mychart = echarts.init(this.$refs.baseLineCharts)
      this.mychart.setOption(option);
      let mychart = echarts.init(this.$refs.baseLineCharts, true)  //监听窗口变化重新加载
      window.addEventListener("resize", mychart.resize); //监听窗口变化重新加载

      //点击事件
      let _this = this
      this.mychart.off('click');
      this.mychart.on('click', function (params) {
        window.console.log(params)
      })
    },
  }
}
</script>

<style scoped lang="scss">
.base-line-charts {
  height: 100%;
  width: 100%;
}
</style>