<template>
    <div class="distributionCharts">
        <div class="content25">
            <el-row :gutter="10">
                <el-col :span="12">
                    <div class="chartsCon50">
                        <div class="chartsItem">
                            <div class="charts-title">用电类别分布</div>
                            <base-pie-charts :chartsData="ydlbArray" :xNameData="ydlbXdata" v-if="show1" style="height: calc(100% - 30px)"></base-pie-charts>
                        </div>
                        <div class="chartsItem">
                            <div class="charts-title">电压等级分布</div>
                            <base-pie-charts :chartsData="dydjArray" :xNameData="dydjXdata" v-if="show2" style="height: calc(100% - 30px)"></base-pie-charts>
                        </div>
                    </div>
                </el-col>
                <el-col :span="12">
                    <div class="chartsCon50">
                        <div class="chartsItem">
                            <div class="charts-title">95598来电次数分布</div>
                            <base-bar-charts
                                :chartsData="ldcsArray" :xNameData="ldcsXdata" v-if="show3"
                                style="height: calc(100% - 30px)"
                            ></base-bar-charts>
                        </div>
                    </div>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col :span="12">
                    <div class="chartsCon50">
                        <div class="chartsItem">
                            <div class="charts-title">停电总时长分布</div>
                            <base-bar-charts :chartsData="tdzscArray" :xNameData="tdzscXdata" v-if="show4" style="height: calc(100% - 30px)"></base-bar-charts>
                        </div>
                    </div>
                </el-col>
                <el-col :span="12">
                    <div class="chartsCon50">
                        <div class="chartsItem">
                            <div class="charts-title">停电评价间隔时长分布</div>
                            <base-bar-charts :chartsData="tdpjArray" :xNameData="tdpjXdata" v-if="show5" style="height: calc(100% - 30px)"></base-bar-charts>
                        </div>
                    </div>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col :span="12">
                    <div class="chartsCon50">
                        <div class="chartsItem">
                            <div class="charts-title">是否发生历史停电事件来电分布</div>
                            <base-pie-charts :chartsData="ifLstdArray" :xNameData="ifLstdXdata" v-if="show6" style="height: calc(100% - 30px)"></base-pie-charts>
                        </div>
                    </div>
                </el-col>
                <el-col :span="12">
                    <div class="chartsCon50">
                        <div class="chartsItem">
                            <div class="charts-title">未来15天停电敏感分布</div>
                            <base-line-charts :chartsData="tdmgArray" :xNameData="tdmgXdata" v-if="show7" style="height: calc(100% - 30px)"></base-line-charts>
                        </div>
                    </div>
                </el-col>
            </el-row>
        </div>
    </div>
</template>

<script>
  import baseBarCharts from '@/components/charts/base-bar-charts'
  import baseLineCharts from '@/components/charts/base-line-charts'
  import basePieCharts from '@/components/charts/base-pie-charts'
  import Interface from '@/base/mixin/power-sensitive-users';

  export default {
    name: "distributionCharts",
    mixins: [Interface],
    components: {
      baseBarCharts,
      baseLineCharts,
      basePieCharts,
    },
    props: {},
    data() {
      return {
        //用电类别分布
        ydlbArray:[0,0],
        ydlbXdata:[0,0],
        show1 : false,
        //电压等级分布
        dydjArray:[0,0],
        dydjXdata:[0,0],
        show2 : false,
        //95598来电次数分布
        ldcsArray:[0,0],
        ldcsXdata:[0,0],
        show3 : false,
        //停电总时长分布
        tdzscArray:[0,0],
        tdzscXdata:[0,0],
        show4 : false,
        //停电评价间隔时长分布
        tdpjArray:[0,0],
        tdpjXdata:[0,0],
        show5 : false,
        //是否发生历史停电事件来电分布
        ifLstdArray:[0,0],
        ifLstdXdata:[0,0],
        show6 : false,
        //未来15天停电敏感分布
        tdmgArray:[0,0],
        tdmgXdata:[0,0],
        show7 : false,
      }
    },
    mounted() {
      this.getTdlbChartsData()
      this.getDydjChartsData()
      this.getLdcsChartsData()
      this.getTdzscChartsData()
      this.getTdpjChartsData()
      this.getIfLstdChartsData() //6
      this.getTdmgChartsData() //6
    },
    methods: {
      //用电类别分布
      getTdlbChartsData() {
        this.show1 = false
        this.ydlbGraph({}).then(res => {
          this.ydlbArray = []
          this.ydlbXdata = []
          res.forEach(item=>{
            this.ydlbArray.push({
              name:item.ydlb,
              value:item.count
            })
            this.ydlbXdata.push(item.ydlb)
          })
          this.show1 = true
        })
      },
      // //电压等级分布
      getDydjChartsData() {
        this.show2 = false
        this.dydjGraph({}).then(res => {
          this.dydjArray = []
          this.dydjXdata = []
          res.forEach(item=>{
            this.dydjArray.push({
              name:item.dydj,
              value:item.count
            })
            this.dydjXdata.push(item.ydlb)
          })
          this.show2 = true
        })
      },
      // // 95598来电次数分布
      getLdcsChartsData() {
        this.show3 = false
        this.ldcsArray = []
        this.ldcsXdata = []
        this.ldcsGraph({}).then(res => {
          res.forEach(item=>{
            this.ldcsArray.push({
              name:item.ldcs+'次',
              value:item.count
            })
            this.ldcsXdata.push(item.ldcs+'次')
          })
          this.show3 = true
        })
      },
      // // 停电总时长分布
      getTdzscChartsData() {
        this.show4 = false
        this.tdzscGraph({}).then(res => {
          this.tdzscArray = []
          this.tdzscXdata = ['4小时以内','4-8小时','8-24小时','24-48小时','48小时以上']
          res.forEach((item)=>{
            this.tdzscArray.push(item)
          })
          this.show4 = true
        })
      },
      // // 停电评价间隔时长分布
      getTdpjChartsData() {
        this.show5 = false
        this.tdpjjgscGraph({}).then(res => {
          this.tdpjArray = []
          this.tdpjXdata = ['180天以上','90-180天','60-90天','30-60天','15-30天','8-15天','2-8天','1-2天','小于1天']
          res.forEach((item)=>{
            this.tdpjArray.push(item)
          })
          this.show5 = true
        })
      },
      // // 未来15天停电敏感分布
      getTdmgChartsData() {
        this.show7 = false
        this.wlsutdmgGraph({}).then(res => {
          window.console.log(res,'999')
          this.tdmgArray = []
          this.tdmgXdata = ['0-0.1','0.1-0.3','0.3-0.5','0.5-0.7','0.7-0.9','0.9-1']
          res.forEach((item)=>{
            this.tdmgArray.push(item)
          })
          this.show7 = true
        })
      },
      // //是否发生历史停电事件来电分布
      getIfLstdChartsData() {
        this.show6 = false
        this.sffslstdGraph({}).then(res => {
          this.ifLstdArray = []
          this.ifLstdXdata = []
          res.forEach((item,index)=>{
            if (index===0){
              this.ifLstdArray.push({
                name:'发生过历史停电事件来电',
                value:item.count
              })
            }else if (index===1){
              this.ifLstdArray.push({
                name:'未发生过历史停电事件来电',
                value:item.count
              })
            }
          })
          this.ifLstdXdata.push('发生过历史停电事件来电','未发生过历史停电事件来电')
          this.show6 = true
        })
      },
    }
  }
</script>

<style scoped lang="scss">
    .distributionCharts {
        .chartsCon50 {
            display: flex;
            justify-content: space-around;
            .charts-title{
                padding-left: 10px;
                height: 20px;
                line-height: 20px;
                font-weight: bold;
                border-left: 5px solid #1A84F4;
                margin-top: 10px;
            }
            .chartsItem {
                flex: 1;
                height: 360px;
            }
        }
    }
</style>